<template>
	<div class="goods">
		<top></top>
		<div class="content">
			<transition name="slide-fade">
				<div class="goods_add" v-if="shown">
					<div style="display: flex;justify-content: space-between">
						<div>
							<a href="JavaScript:void(0)" @click="add" class="button">
								<i class="iconfont icon-iconjia"></i>
								<span>添加新的商品</span>
							</a><br />
							<span class="content_tishi">你可以添加最多500件商品</span>
						</div>
						<div>
							<Select v-model="model1" style="width:200px" @on-change="seeList">
								<Option v-for="item in list" :value="item.value" :key="item.value">{{ item.label }}</Option>
							</Select>
						</div>
					</div>
					<p v-if="noGoods">还没有添加商品</p>
					<div class="all">
						<div v-for="(item,index) in goodsLists" @mouseenter="enter(index)" @mouseleave="out(index)" style="margin:0 0.2rem 0.2rem 0;">
							<div class="goods_zhe" v-if="item.show">
								<div class="close">
									<Icon type="ios-close" size="25" @click.native="close(index,item)" color="#969696"></Icon>
								</div>
								<p class="goods_center"><span @click="editor(index,item)">编辑商品</span></p>
							</div>
							<div class="goods_one">
								<img :src="item.pic" alt="">
								<div class="foot">
									<p>{{item.name}}</p>
									<p v-if="item.prices == false">￥{{item.price}}</p>
									<p v-else>￥{{item.prices}}</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</transition>
			<transition name="slide-fade">
				<add v-if="show" :sortsInfo="info"></add>
			</transition>
			<transition name="slide-fade">
				<changeInfo v-if="changeShow" :sortsInfo="info"></changeInfo>
			</transition>
		</div>
	</div>
</template>
<script>
	import top from '../../top.vue'
	import add from './addGoods.vue'
	import changeInfo from './changeGoods.vue'
	export default {
		data() {
			return {
				src: '',
				noGoods: false,
				info: [],
				goodsLists: [],
				model1: '',
				list: [{
						value: '1',
						label: '商品分类(0)'
					},
					{
						value: '2',
						label: '没有分类(0)'
					}
				],
				shown: true,
				show: false,
				changeShow: false
			}
		},
		methods: {
      seeList(){
        this.getGoodList(this.model1);
      },
			out(index) {
				this.goodsLists[index].show = false;
				this.goodsLists.splice(index, 1, this.goodsLists[index]);
			},
			close(index, item) {
				this.$Modal.confirm({
					title: '删除商品',
					content: '<p>确认要删除该商品吗?</p>',
					onOk: () => {
						this.post("custom/Shop/delUserGoods", {
							appid: this.$route.params.appid,
							good_id: item.id,
						}).then(res => {
							if(res.data.code === 10000) {
								const content = res.data.msg;
								this.$Modal.error({
									title: "删除成功",
									content: content
								});
                this.getGoodList();
							}
						})
					},
					onCancel: () => {

					}
				});
			}, //      编辑某个商品
			editor(index, item) {
				this.$store.commit('setStatus', false);
				this.shown = false;
				this.show = false;
				this.changeShow = true;
				sessionStorage.setItem("id", item.id);
			},
			enter(index) {
				this.goodsLists[index].show = true;
				this.goodsLists.splice(index, 1, this.goodsLists[index]);
			},
			add() {
				this.$store.commit('setStatus', false);
				this.$store.dispatch('imgLists', []);
				this.show = true;
				this.shown = false;
				this.changeShow = false;
			},
//      获取商品列表
      getGoodList(id){
        this.post('custom/Shop/getUserGoods', {
          page: 1,
          number: 100,
          cate_id:id
        }).then(res => {
          if(res.data.code === 10000) {
            this.goodsLists = res.data.data.info;
            this.goodsLists.forEach(item => {
              item.pic = "http://weapp.xiguawenhua.com/" + item.pic.split(',')[0];
              item["show"] = false
            });
            window.frames[0].postMessage(this.goodsLists,"*");
          }else{
            this.$Modal.error({
              title: "失败",
              content: res.data.msg
            });
          }
        })
      }
		},

		mounted() {
			//         商品分类
        this.getGoodList();

		},
		created() {
			this.post('custom/Shop/getCateList', {}).then(res => {
				if(res.data.code === 10000) {
					if(res.data.data.info.length === 0) {
              return false;
					} else {
						this.list = res.data.data.info;
						this.list.forEach((item, index) => {
							item['value'] = item.id;
							item['label'] = item.name;
						});
						this.info = this.list;
					}
				}
			})
		},
		components: {
			top,
			add,
			changeInfo
		}
	}
</script>
<style scoped>
	.goods {
		position: relative;
	}

	.content {
		height: calc(100vh - 78px - 92px);
		padding: 0.2rem 0;
		overflow-y: auto
	}
  .content::-webkit-scrollbar{
    width:0.05rem;
    height:0.1rem;
  }
  .content::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: rgba(0, 0, 0, 0.2);
  } /* 滑块颜色 */
	.goods_add a {
		display: inline-block;
		padding: 0.1rem 0.2rem;
		background: #59A2ED;
		color: white;
		font-size: 0.14rem;
		border-radius: 0.03rem
	}

	.goods_add>span {
		display: inline-block;
		font-size: 0.14rem;
		padding: 0.1rem;
	}

	.goods_add>p {
		padding: 0.2rem 0;
		font-size: 0.13rem
	}

	.slide-fade-enter-active {
		transition: all 1s ease;
	}

	.slide-fade-enter {
		transform: translateX(70%);
	}

	.all {
		display: flex;
		flex-wrap: wrap;
    margin-top: 0.1rem;
	}

	.goods_one {
		width: 1.66rem;
		height: 2.2rem;
		border-radius: 0.03rem;
		border: 1px solid #eeeeee;
		cursor: pointer;
	  text-align: center;
		background: white;
		box-shadow: 5px 5px 10px #E7E7E7;
	}

	.goods_one>img {
		height: 1.7rem;
		width: 1.6rem;
	}

	.foot {
	  text-align: left;
	  color: #111111;
		margin: 0.05rem 0.15rem;
		padding-bottom: 0.2rem;
	}

	.goods_zhe {
		position: absolute;
		width: 1.66rem;
    height: 2.2rem;
		border: 1px solid #f3f3f3;
		cursor: pointer;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		z-index: 10;
		background-color: rgba(246, 246, 246, 0.5);
	}

	.all>div {
		width: 1.66rem;
    height: 2.2rem;
		display: flex;
		justify-content: center;
		align-items: center;
    position: relative;
	}

	.close {
		align-self: flex-end;
		margin-top: 0.2rem;
		margin-right: 0.2rem;
	}

	.goods_center {
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.goods_center>span {
		color: white;
		display: inline-block;
		padding: 0.05rem 0.1rem;
		background-color: rgba(0, 0, 0, 0.5);
		opacity: 0.8;
    margin-bottom:0.5rem;
	}
	.content_tishi{
	  color:#606060;
	  letter-spacing: 0.02rem;
	  margin: 0.1rem 0.02rem;
	  display: inline-block;
	}
</style>



