<template>
  <div  ref="box">
    <div class="finish_photo">
      <div>
        <p class="font">
          这里是你上传的所有图片,你可以上传更多图片，然后从列表中选择
        </p>
      </div>
      <a href="javascript:void(0)" v-if="goods===0" style="margin-right: 0.1rem"><Icon type="checkmark" size="20" color="white"></Icon><span style="margin-left: 0.1rem">上传图片</span></a>
      <a href="javascript:void(0)" v-else-if="goods===3" style="margin-right: 0.1rem"><Icon type="checkmark" size="20" color="white"></Icon><span style="margin-left: 0.1rem">上传图片</span></a>
      <a href="javascript:void(0)" class="btn"  @click="selectAll" v-else><Icon type="checkmark" size="20" color="white"></Icon><span style="margin-left: 0.1rem">添加选定图片</span></a>
    </div>
    <div class="box_photo">
        <input class="imgup" type="file" accept="image/png,image/gif,image/jpeg" ref="file" @change.self="upload">
          <div icon="my-el-icon-plus" class="el-icon-plus">
              <img src="../../assets/jiahao.png"/>
              <p>上传照片</p>
          </div>
      <div  v-for="(item,key) in photoList" class="photo_list" @click="select(key)" ref="key">
        <img :src="item.pic" alt="" style="width: 150px;max-height: 100%;">
        <p class="icon" v-if="item.show"><Icon type="checkmark" size="23" color="#93b719"></Icon></p>
      </div>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          photoList:[],
          arr:[]
        }
    },
  methods:{
    select(key){
        if(this.goods===1 || this.goods===2){
            if(this.photoList[key]["show"]){
              this.photoList[key]["show"]=false;
              this.photoList.splice(key,1,this.photoList[key]);
              this.arr.splice(key,1,"");
            }else{
              this.photoList[key]["show"]=true;
              this.photoList.splice(key,1,this.photoList[key]);
              this.arr.splice(key,1,this.photoList[key]);
            }
        }else if (this.goods===3){
          this.photoList.forEach(item=>{
             item.show=false;
          });
          this.photoList[key]["show"]=true;
          this.$store.dispatch('photoed',this.photoList[key]);
          let obj={};
          obj["list"]=[
            {
              value: '1',
              label: '无连接'
            },{
              value: '2',
              label: '商品',
            },{
              value: '3',
              label: '分类',
            },{
              value: '4',
              label: '界面',
            }];
          obj["link"]='';
          obj['good']='';
          obj['pic']=this.photoList[key].pic;
          this.$store.dispatch('carousel',obj);
          window.frames[0].postMessage(obj,'*');
          this.$emit('close',false);
        } else if (this.goods === 4){
          let imgUrl = this.photoList[key].pic;
          let imgIndex = -1;
          this.$store.dispatch('hotelHomeImg',{imgUrl,imgIndex});
          this.$emit('close',false);
        }else if(this.goods === 0){
          this.photoList.forEach(item=>{
            item.show=false;
          });
          this.photoList[key]["show"]=true;
          this.$store.dispatch('photoed',this.photoList[key]);
          this.$emit('close',false);
        }
    },
    selectAll(){
      let arrList=[];
      arrList=this.arr.filter(item=>{
          return item !=="";
      });
      if(arrList.length>10){
          alert('最多只能选择10张图片')
      }else{
          arrList.forEach(item=>{
            return  item["show"]=false;
          });
          if(this.goods===1){
            this.$store.dispatch('imgLists',arrList);
            this.$emit('close',false);
          }else if(this.goods===2){
            this.$store.dispatch('goodsInfo',arrList);
            this.$emit('close',false);
          }
      }
    },
//  图片上传
    upload(){
      const that = this;
      lrz(this.$refs.file.files[0])
        .then(function (rst) {
          let obj={};
          obj["pic"]=rst.base64;
          that.$store.dispatch('photoed',obj);
//            const index=rst.base64.indexOf(',');
//            const newBase=rst.base64.slice(index+1);
          that.post('/custom/base/pic_upload',
            {
              image: rst.base64
            })
            .then((res)=>{
              if(res.data.code===10000){
                if(that.goods===1){
                  let arr=[];
                  let obj={};
                  obj["pic"]="http://weapp.xiguawenhua.com/"+res.data.data;
                  obj["show"]=false;
                  arr.push(obj);
                  that.$store.dispatch("imgLists",arr);
                  that.$emit('close',false);
                }else if(that.goods===2){
                  let arr=[];
                  let obj={};
                  obj["pic"]="http://weapp.xiguawenhua.com/"+res.data.data;
                  obj["show"]=false;
                  arr.push(obj);
                  that.$store.dispatch("goodsInfo",arr);
                  that.$emit('close',false);
                } else if(that.goods===3){
                  let obj={};
                  obj["list"]=[
                    {
                      value: '1',
                      label: '无连接'
                    },{
                      value: '2',
                      label: '商品',
                    },{
                      value: '3',
                      label: '分类',
                    },{
                      value: '4',
                      label: '界面',
                    }];
                  obj["link"]='';
                  obj['good']='';
                  obj['pic']="http://weapp.xiguawenhua.com/"+res.data.data;
                  this.$store.dispatch('carousel',obj);
                  window.frames[0].postMessage(obj,'*');
                }else{
                  that.$store.dispatch('Photo',res.data.data);
                  that.$emit('close',false);
                }
              }else{
                  const content = res.data.msg_test;
                  this.$Modal.error({
                    title: "上传失败",
                    content: content
                  });
              }
            })
        })
    },
  },
  computed:{
      goods(){
          return this.$store.getters.getGoodsPhoto;
      }
  },
  props:['photo'],
  watch:{
        photo(val){
          return val;
        }
  },
  mounted(){
    this.post('/custom/base/pic_list',{}).then(res=>{
       if(res.data.code===10000){
           this.photoList=res.data.data;
           this.photoList.forEach(item=>{
              item["show"]=false;
             item.pic="http://weapp.xiguawenhua.com/"+item.pic
             })
       }
    })
  }
}
</script>
<style scoped="scoped">
  .finish_photo{  display: flex;  justify-content: space-between;  align-items: center;  padding: 0.2rem 0;  }
  .font{ color: #666666;  font-weight: 600;  }
  .btn{  color: white;  padding: 0.1rem 0.2rem;  background-color: #93b719;  border-radius: 0.05rem;  display: flex;  justify-content: center;  align-items: center;  margin-right: 0.15rem;  }
	.box_photo{  display: flex;  flex-wrap: wrap;  }
  .photo_list{  width: 158px;  height:158px;  margin-left:20px;  cursor: pointer;  line-height: 158px;  text-align: center;  position: relative;  }
  .photo_list:hover{  width: 160px;  border: 1px solid #93b719;  }
  .icon{  position: absolute;  top:-60px;  left:10px;  }
  .active{  width: 160px;  border: 1px solid #93b719;  }
  .imgup{  position: absolute;  display: inline-block;  z-index: 10;  opacity: 0;  width: 1.5rem;  height: 1.5rem;  cursor: pointer;  }
	.el-icon-plus{  width: 1.5rem;  height: 1.5rem;  border: 0.02rem dashed #aaaaaa;  text-align: center;  font-size: 0.18rem;  color: #868C92;  cursor: pointer;  }
	.el-icon-plus:hover{  background: #DADADA;  }
	.el-icon-plus>img{  display: block;  margin: 0.5rem auto;  }
	.el-icon-plus>p:nth-child(2){  font-size: 0.12rem;  margin-top: -0.4rem;  }

</style>



