<template>
  <div>
    <div class="add_shangpin">
      <h1>商品信息</h1>
      <section class="info">
        <div class="left">
          <div class="left_info">
            <strong>图片</strong>
            <a href="javascript:void(0)" @click="upload" v-if="images.length!==0">+添加图片</a>
          </div>
          <div  class="left_photo">
            <div class="left_photo_one" v-if="images.length===0" @click="upload">
              <Icon type="images" size="50" color="#dddddd"></Icon>
              <h2 style="color: #dddddd;">上传图片</h2>
              <p style="color: #dddddd">最多可增加十张图片</p>
            </div>
            <div class="single-image-wrapper" v-else-if="images.length===1" @mouseover="shade">
              <div class="shade" v-if="shadeShow" @mouseout="cancleShade">
                <a href="javascript:void (0)"  @click="upload"><Icon type="loop" size="35" color="#f3f3f3" style="margin:0 0.25rem"></Icon></a>
                <a href="javascript:void (0)" @click="deletePhoto"><Icon type="android-delete" size="35" color="#f3f3f3"></Icon></a>
              </div>
              <img :src="images[0].pic" alt="" style="width: 100%;height: 100%">
            </div>
            <div class="images-wrapper" v-else>
              <div style="position: relative;display: flex;flex-wrap: wrap;flex-direction: row">
                <div class="image-item place-holder" @click="upload">+</div>
                <div class="image-item" @mouseenter="imageItems(key)" @mouseleave="cancleImageItems(key)" v-for="(item,key) in images">
                  <div class="image-cover" v-if="item.show" >
                    <Tooltip content="将图片设置成封面图片" placement="top">
                      <a href="javascript:void(0)" @click="setImage(key)"><Icon type="image" size="25" color="#f3f3f3" v-if="key!==0"></Icon></a>
                    </Tooltip>
                    <a href="javascript:void (0)"  @click="upload"><Icon type="loop" size="25" color="#f3f3f3" style="margin:0 0.25rem"></Icon></a>
                    <a href="javascript:void (0)" @click="deletePhoto(key)"><Icon type="android-delete" size="25" color="#f3f3f3"></Icon></a>
                  </div>
                  <img :src="item.pic" alt="" style="width:1.45rem;max-height: 100%">
                </div>
              </div>
            </div>
          </div>
        </div>
      <div class="right">
          <div>
            <strong>商品名称</strong>
            <span style="color: red;float: right;margin-bottom: -0.02rem;display: inline-block">{{msg}}</span>
          </div>
          <div>
            <input v-model.trim="name" placeholder="商品名称" @blur="change" ref="input" type="text" @input="input">
            <div style="margin-top: 0.05rem">
              <strong>商品简介</strong>
              <span style="float: right;font-size: 0.14rem;color: darkgrey;">{{textNum}}</span>
            </div>
            <textarea  v-model.trim="text" cols="30" rows="10" class="text" placeholder="简单描述下你的商品吧。 (选填)" ref="text" @focus="textF"  @blur="change"  @input="inputLength" maxlength="600"></textarea>
          </div>
        </div>
      </section>
    </div>
    <div class="add_se">
    <p v-if=""><strong>商品价格及商品规格</strong></p>
    <div class="price" v-for="(item,key) in lists">
      <div style="margin-right: 0.2rem;" v-if="item.show">
        <p v-if="key===0"><strong style="font-size: 0.13rem">商品规格</strong></p>
        <Input v-model.trim="item.name" placeholder="颜色、规格等等" style="width: 200px"></Input>
      </div>
      <div>
        <p  v-if="key===0"><strong style="font-size: 0.13rem">价格</strong></p>
        <Input v-model.trim="item.price" placeholder="价格."  style="width: 200px"></Input>
      </div>
      <div style="margin-left: 0.2rem;">
        <p v-if="key===0"><strong style="font-size: 0.13rem">库存</strong></p>
        <div style="display: flex;align-items: center">
          <Input v-model.trim="item.lastNum"  style="width: 300px;font-size: 0.16rem" :disabled="item.disable">
          <Checkbox v-model="item.single"  slot="append" @on-change="control(key)">无购买数量限制</Checkbox>
          </Input>
          <Icon type="ios-trash" size="20" style=";cursor: pointer;margin-left: 0.1rem" @click.native="close(key)" v-if="item.show"></Icon>
        </div>
      </div>
    </div>
    <Button  icon="plus-round" @click="add">添加商品规格</Button>
    </div>
    <div class="add_se">
    <p style="margin-top: 0.2rem"><strong>商品分类</strong></p>
    <div class="sorts">
      <Select v-model="sorts" style="width:200px" v-if="sortsInfo.length!==0">
        <Option v-for="item in sortsInfo" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
      <a href="JavaScript:void (0)" @click="addSort" v-if="sortsInfo.length===0">添加商品分类(可选)</a><span>可以使您的访客更方便的找到所需商品.</span>
      <Tooltip placement="right" content="管理商品分类请前往商品分类">
        <Icon type="help-circled" size="18"></Icon>
      </Tooltip>
    </div>
    <p style="margin-top: 0.2rem"><strong>配送</strong></p>
    <div class="delivery">
      <Checkbox v-model="is_deliver" false-value="0"  true-value="1"><span style="font-size: 0.14rem">买家需要填写快递信息吗?</span></Checkbox>
      <Tooltip placement="right" content="在'配送'里修改运费收取方式'">
        <Icon type="help-circled" size="18"></Icon>
      </Tooltip>
    </div>
    <p style="margin-top: 0.2rem"><strong>商品详情页描述</strong></p>
    <div class="info">
      <Checkbox v-model="content_show" false-value="0"  true-value="1"><span style="font-size: 0.14rem">在商品详情页上显示更多详情</span></Checkbox>
    </div>
    <text-editor @inputInfo="goodsInfo" v-if="content_show==='1'" :value="editorContent"></text-editor>
    <footer class="footer" style="z-index: 99;">
      <a href="javaScript:void(0)" @click="cancel">< 取消</a>
      <a href="javaScript:void(0)" @click="submit" v-if="!load">保存</a>
      <a href="javascript:void(0)"  v-if="load">保存 <img src="../../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></a>
    </footer>
    </div>
    <modal ref="upload"></modal>
  </div>
</template>
<script>
  import modal from '../../upload/modal.vue'
  import textEditor from '../../textEditor.vue'
  export default{
    data(){
      return{
        load: false,
        shadeShow:false,
        model1:'',
        modal:false,
        value:'',
        //提示信息
        single:true,
        singleInfo:false,
        msg:"",
        text:'',
        textNum:600,
        name:"",
        price:'',
        imgList:[],
        desc:'',
        dis_price:"",
        lists:[{name:'',price:'',single:true, disable:true, lastNum:'∞',show:false,}],
        stock:"",
        sorts:'',
        is_deliver:'1',
        content_show:"",
        content:[],
        src:[],
      }
    },
    computed:{
      photo(){
        return this.$store.getters.getPhoto;
      },
      photoed(){
        if(this.$store.getters.getPhotoed){
          return this.$store.getters.getPhotoed.pic;
        }else{
          return this.src
        }
      },
      images(){
        return this.$store.getters.getImgLists;
      }
    },
    props:['sortsInfo'],
    methods:{
      goodsInfo(val){
        this.content=val
      },
      setImage(key){
        let src=this.images[0];
        this.images[0]=this.images[key];
        this.images.splice(key,1,src)
      },
//        多张图片显示
      imageItems(key){
        this.images[key].show=true;
        this.images.splice(key,1,this.images[key]);
      },
//      多张图片隐藏
      cancleImageItems(key){
        this.images[key].show=false;
        this.images.splice(key,1,this.images[key]);
      },
      deletePhoto(key){
        this.$store.commit('deleteImgLists',key);
      },
      shade(){
        this.shadeShow=true;
      },
      cancleShade(){
        this.shadeShow=false;
      },
//       添加图片
      addPhoto(){
        this.$refs.upload.modal=true;
      },
      change1(){

      },
      //上传图片
      upload(){
        this.$refs.upload.modal=true;
        this.$store.dispatch('goodsPhoto',1);
      },
      addSort(){
        this.$router.push('sorts')
      },
      inputLength(){
//        this.textNum-=this.text.length;
      },
      add(){
        this.lists[0].show=true;
        this.lists.push({single:true, disable:true,lastNum:'∞',show:true,});
      },
//          聚焦
      textF(){
        this.$refs.text.style.borderColor='skyblue';
      },
//      输入设置
      input(){
        this.$refs.input.style.borderColor='skyblue';
        this.msg=''
      },
//      检测
      change(){
        this.$refs.text.style.borderColor='gainsboro';
        if(this.name){
          this.$refs.input.style.borderColor='gainsboro';
        }else{
          this.$refs.input.style.borderColor='red';
          this.msg='商品名称不能为空'
        }
      },
      control(key){
        if(!this.lists[key].disable){
          this.lists[key].disable=true;
          this.lists[key].lastNum='∞';
        }else{
          this.lists[key].disable=false;
          this.lists[key].lastNum='1';
        }
      },
      close(key){
        if(this.lists.length===2){
          this.lists.splice(key,1);
          this.lists[0].show=false;
        }else if(this.lists.length===1){

        }else{
          this.lists.splice(key,1);
        }
      },
      cancel(){
        this.$store.commit('setStatus',true);
        this.$parent.shown=true;
        this.$parent.show=false;
        this.$parent.changeShow=false;
      },
      submit(){
        if (this.name) {
          this.load = false ;
          if (this.lists.length === 1) {
            this.price = this.lists[0].price;
            this.dis_price = this.lists[0].price;
            this.stock = this.lists[0].lastNum;
            if (this.lists[0].lastNum === "∞") {
              this.stock = -1;
              this.lists = [];
            }else{
              this.lists = [];
            }
          } else {
            this.price = "";
          }
          let arr = [];
          arr = this.images.map(item => {
            return item = item.pic.slice(29)
          });
          this.post("custom/Shop/updateUserGood", {
            name: this.name,
            price: this.price,
            pic: arr.toString(),
            desc: this.text,
            dis_price: this.dis_price,
            spec: JSON.stringify(this.lists),
            stock: this.stock,
            cid: this.sorts,
            is_deliver: this.is_deliver,
            content_show: this.content_show,
            content: JSON.stringify(this.content),
            good_id: sessionStorage.getItem('id')
          }).then(res => {
            this.load = true ;
            if (res.data.code === 10000) {
              const content = res.data.msg;
              this.$Modal.success({
                title: "添加成功",
                content: content
              });
              this.cancel();
              this.$parent.getGoodList();
            }else{
              const content = res.data.msg;
              this.$Modal.success({
                title: "成功",
                content: content
              });
            }
          })
        }
        },
      changeGoods(){

      }
    },
    components:{modal,textEditor},

    watch:{
      text(newVal,oldVal){
          this.textNum-=(newVal.length-oldVal.length);
      },
      sortsInfo(val){
        return val;
      }
    },
    mounted(){
      this.post("custom/Shop/getUserGoodById",{
        good_id:sessionStorage.getItem("id")
      }).then(res=>{
        if (res.data.code===10000){
          this.name=res.data.data.name;
          this.price=res.data.data.price;
          this.text=res.data.data.desc;
          this.dis_price=res.data.data.dis_price;
          this.sorts=res.data.data.cid-0;
          this.is_deliver=`${res.data.data.is_deliver}`;
          this.content_show=`${res.data.data.content_show}`;
//          this.content=JSON.parse(res.data.data.content);
          if(res.data.data.content){
            this.editorContent = JSON.parse(res.data.data.content);
          }
          res.data.data.pic.split(',').forEach(item=>{
              let obj={};
              obj["pic"]="http://weapp.xiguawenhua.com/"+item;
              obj["show"]=false;
              this.src.push(obj);
          });
          this.$store.dispatch('imgLists',this.src);
          if (res.data.data.stock === 0){
             this.lists = JSON.parse(res.data.data.spec);
          }else{
            this.lists[0].price=this.price;
          }
        }
      })
    }
  }
</script>
<style scoped>
  @import '../../../../static/store/addGoods.css';
</style>



