<template>
<div>
  <Modal v-model="modal" width="800">
    <p slot="header" style="text-align:left">
      <Icon type="ios-photos"></Icon>
      <span>上传图片</span>
    </p>
    <div class="modal_upload">
      <Tabs type="card">
        <TabPane label="上传新图片" icon="android-upload">
          <upload @close="closeModal"></upload>
        </TabPane>
        <TabPane label="已上传图片" icon="ios-folder">
          <div class="finnish">
            <finish @close="closeModal"></finish>
          </div>
        </TabPane>
      </Tabs>
    </div>
    <div slot="footer">
    </div>
  </Modal>
</div>
</template>
<script>
  import upload from './upload.vue'
  import finish from './finish.vue'
export default{
    data(){
        return {
          modal:false,
          lists:[],
        }
    },
  components:{
    upload,
    finish
  },
  mounted(){

  },
  methods:{
    closeModal(val){
      this.modal=false;
    }
  }
}
</script>
<style scoped="scoped">
  .modal_upload{
    text-align:left;
    height: 60vh;
    overflow-y: auto;
 }
  .finnish{
      height: 50vh;
      overflow-y: auto;
  }
  .finnish::-webkit-scrollbar{
    width:0.05rem;
    height:0.1rem;
  }
  .finnish::-webkit-scrollbar-thumb {
    border-radius: 0.1rem;
    background-color: rgba(0, 0, 0, 0.2);
  } /* 滑块颜色 */
</style>



