<template>
<div class="editor">
    <div class="editor_header">
      <a href="javascript:void(0)" class="left" @click="addGoodsPhotos">
        <Icon type="images" size="30" color="black"></Icon>
        <span style="display: inline-block;padding: 0 0.05rem">添加图片</span>
      </a>
      <p style="color:#a9aeb2">您最多可以上传10张图片到产品详情页。</p>
    </div>
  <div contenteditable="true"
       @input="changeText" class="text" ref="text" @blur="save">
      <div v-for="list in value" v-if="list.type ==='text'">
          {{list.content}}
      </div>
      <div v-for="list in value" v-if="list.type==='image'">
        <img :src="list.pic | image" alt="">
      </div>
      <div v-for="item in images">
        <img :src="item.pic | image" alt="" style="width: 4rem;max-height: 100%;">
      </div>
    <!--v-for="item in images"-->
  </div>
  <modal ref="upload"></modal>
</div>
</template>
<script>
 import modal from './upload/modal.vue'
export default{
  props: ['value'],
    data(){
        return {
            data:[],
            innerText:this.value,
            arr:[]
        }
    },
  computed:{
    images(){
        if(this.$store.getters.getGoodsInfo.length === 0){
         return false;
        }else {
//          this.$on('editor',this.save);
          if(this.$refs.text){
            this.$refs.text.focus();
          }
          return this.$store.getters.getGoodsInfo;
        }
    }
  },
  watch:{
    value(val){
      return val
    }
  },
  methods:{
    changeText(){

    },
    save(){
//        图片列表
      let images=this.images;
      let arr=[];
//        刚开始输入文字
      const parents=this.$refs.text;
      const nodeType = parents.firstChild.nodeType;
      if(nodeType===1){
//        元素 第一张一定是图片 或者 div 文本
        let photo=[];
        photo=parents.childNodes;
        let id=0;
        for(let i=0;i<photo.length;i++){
          photo[i].childNodes.forEach(item =>{
            if(item.nodeName === "IMG"){
                    let obj={};
                    obj["type"]="image";
                    obj["pic"]=images[id].pic.slice(29);
                    arr.push(obj);
                    id++;
              }else{
                let obj={};
                obj["type"]="text";
                obj["content"]=item.nodeValue;
                arr.push(obj);
              }
          });
        }
      }else if(nodeType===3){
//        文本节点
        let id=0;
        if(parents.firstChild.nodeValue){
          let obj={};
          obj["type"]="text";
          obj["content"]=parents.firstChild.nodeValue;
          arr.push(obj);
//          全是文本详情
          for(let i=0;i<parents.children.length;i++){
              if(parents.children[i].firstChild.nodeName==="IMG"){
                let obj={};
                obj["type"]="image";
                obj["pic"]=images[id].pic.slice(29);
                arr.push(obj);
                id++;
              }else{
                if( parents.children[i].firstChild.nodeValue){
                  let obj={};
                  obj["type"]="text";
                  obj["content"]=parents.children[i].firstChild.nodeValue;
                  arr.push(obj);
                }
              }
          }
        }
      }
      this.$emit("inputInfo",arr);
    },
    addGoodsPhotos(){
      this.$refs.upload.modal=true;
      this.$store.dispatch('goodsPhoto',2);
    },
  },
  beforeUpdate(){
//    this.$emit("editor");
  },
  mounted(){

  },
  filters:{
    image(val){
        return val.indexOf("http") === -1  ? "http://weapp.xiguawenhua.com/"+ val : val;
    }
  },
  components:{modal}
}
</script>
<style scoped>
  .editor{  width: 9rem;  }
  .editor_header{  padding: 0.1rem;  background: #f2f2f2;  border: 1px solid #f3f3f3;  }
.text{min-height: 200px;  padding: 0.05rem 0.05rem;  border: 1px solid #f3f3f3;font-size: 0.16rem;text-align: left}
.text:focus{border:1px solid #f2f2f2}
.editor_header{  display: flex;  justify-content: space-between;;  align-items: center;  }
  .left{display: flex;align-items: center}
</style>



