<template>
  <div class="up_file">
    <input class="img_up" type="file" ref="file" @change.self="upLoad" :accept="accepts">
    <div icon="my-el-icon-plus" class="el-icon-plus">
      <img src="../../assets/jiahao.png" />
      <p>上传照片</p>
    </div>
  </div>
</template>
<script>
  import lrz from 'lrz'
  export default {
    data() {
      return {
        accepts: 'image/jpeg,image/png,image/gif',
        defaultList: [],
        imgName: '',
        visible: false,
        uploadList: [],
        one: '',
        lists: [],
        i: 0,
        photo: ''
      }
    },
    methods: {
      upLoad() {
        const that = this;
        lrz(this.$refs.file.files[0])
          .then((rst) => {
            let obj = {};
            obj["pic"] = rst.base64;
            that.$store.dispatch('photoed', obj);
            //            const index=rst.base64.indexOf(',');
            //            const newBase=rst.base64.slice(index+1);
            that.post('/custom/base/pic_upload', {
                image: rst.base64
              })
              .then((res) => {
                if(res.data.code === "10000") {
                  if(that.goods === 1) {
                    let arr = [];
                    let obj = {};
                    obj["pic"] = "http://weapp.xiguawenhua.com/" + res.data.data;
                    obj["show"] = false;
                    arr.push(obj);
                    that.$store.dispatch("imgLists", arr);
                    that.$emit('close', false);
                  } else if(that.goods === 2) {
                    let arr = [];
                    let obj = {};
                    obj["pic"] = "http://weapp.xiguawenhua.com/" + res.data.data;
                    obj["show"] = false;
                    arr.push(obj);
                    that.$store.dispatch("goodsInfo", arr);
                    that.$emit('close', false);
                  } else if(that.goods === 3) {
                    let obj = {};
                    obj["list"] = [{
                      value: '1',
                      label: '无连接'
                    }, {
                      value: '2',
                      label: '商品',
                    }, {
                      value: '3',
                      label: '分类',
                    }, {
                      value: '4',
                      label: '界面',
                    }];
                    obj["link"] = '';
                    obj['good'] = '';
                    obj['pic'] = "http://weapp.xiguawenhua.com/" + res.data.data;
                    that.$store.dispatch('carousel', obj);
                  } else if (that.goods === 4){
                    let imgUrl = "http://weapp.xiguawenhua.com/" + res.data.data;
                    let imgIndex = -1
                    that.$store.dispatch('hotelHomeImg',{imgUrl,imgIndex});
                  }else {
                    that.$store.dispatch('Photo', res.data.data);
                    that.$emit('close', false);
                  }
                } else {
                  const content = res.data.msg_test
                  this.$Modal.error({
                    title: "上传失败",
                    content: content
                  });
                }
              })
          })
      }
    },
    computed: {
      //        1 商品 0 基本信息; 2 商品详情
      goods() {
        return this.$store.getters.getGoodsPhoto;
      }
    },
    mounted() {}
  }
</script>
<style scoped="scoped">
  .up_file {
    display: flex;
    justify-content: center;
    margin-top: 20%;
  }

  .up_file>.img_up {
    position: absolute;
    display: inline-block;
    z-index: 10;
    opacity: 0;
    width: 2rem;
    height: 2rem;
    cursor: pointer;
  }

  .el-icon-plus {
    width: 2rem;
    height: 2rem;
    border: 0.02rem dashed #AAAAAA;
    text-align: center;
    font-size: 0.18rem;
    color: #868C92;
    border-radius: 0.08rem;
  }

  .el-icon-plus>img {
    display: block;
    margin: 0.6rem auto;
  }

  .el-icon-plus>p:nth-child(2) {
    font-size: 0.14rem;
    margin-top: -0.4rem;
  }
</style>



